div button{
    align-items: center;
    justify-content: center;
    background: #60bd88;
    color: white;
    border: none;
    border-radius: 5px;
    line-height: 30px;
    box-shadow: 
      -2px -2px 6px rgba(rgb(148, 224, 247), .6),
      2px 2px 12px #fcf270;
}
.box  span{
  background-color: #819efc;
  border-radius: 4px;
  color: white;
  padding: 12px;
  width: 250px;
  border: none;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
  -webkit-appearance: button;
  appearance: button;
  outline: none;
}

  .box select {
    width: 100%;
    background-color: #819efc;
    border-radius: 4px;
    color: white;
    padding: 12px;
    width: 250px;
    border: none;
    box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
    -webkit-appearance: button;
    appearance: button;
    outline: none;
  }
  
