  .icon {
    min-width: 46px;
    min-height: 46px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 12px;
    box-shadow: 
      -2px -2px 6px rgba(rgb(148, 224, 247), .6),
      2px 2px 12px #fcf270;
      
    
  }
 .icon svg {
    transform: translate(-1px, -1px);    
  }